import personService from './personServices'

const sameName = (PersonObject,persons,setPersons) => {
    let test = persons.find(person => person.name.toLowerCase() === PersonObject.name.toLowerCase())
   let tempID = test.id;

    const line = `${test.name} is already added to
    the phonebook, replce the old number with a new one?`

    let result = window.confirm(line)
    if(result){
        personService.update(tempID,PersonObject)
        .then( updatedPerson =>
            setPersons(persons.map(person => 
                person.id !== tempID ? person:updatedPerson))
        )
        return true
    }
    return false
}

export default {sameName}