import React, { useEffect, useState } from 'react'
import PersonMapper from './components/personMapper'
import personService from './services/personServices'
import axios from 'axios'
import Namer from './services/SameName'
import sameName from './services/SameName'
import Notification from './components/Notification'


const App = () => {
  const [ persons, setPersons ] = useState([ ]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ filter, setNewFilter ] = useState('')
  const [message, setMessage] = useState('')
  const [status, setStatus] = useState(true)
  
  //inital retrieval of data
  useEffect(() => {
    personService
    .getAll()
    .then(initialppl => 
      setPersons(persons.concat(initialppl)))
  },[])


  const addPerson = (event) => {
    event.preventDefault()

    let valid = false
    let done = false

    const PersonObject = {
      name: newName,
      number: newNumber
    }

    persons.forEach((person) => {
      if(person.name.toLowerCase() === newName.toLowerCase()){
        valid = true
      }
    })

    if(valid){
        done = Namer.sameName(PersonObject,persons,setPersons)
    } else{
      done = true
    personService
    .create(PersonObject)
    .then(newperson => {
      setPersons(persons.concat(newperson))
      setNewName('')
      setNewNumber('')
    })
  } 
  if(done){
    setStatus(true)
    setMessage(`${PersonObject.name} has been added`)
    setTimeout(()=>{
      setMessage(null)
    },5000)
  }

}
  
  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    setNewFilter(event.target.value)
  }


  return (
    <div>
      <h1>Phonebook</h1>
      <Notification message={message} status={status} />
      <form> 
        <div>filter shown with: <input value={filter} onChange = {handleFilterChange}/></div>
        </form>

      <h2> Add a new Contact</h2>
      <form onSubmit={addPerson}>
        <div>name: <input value={newName} onChange = {handleNameChange}/></div>
        <div>number: <input value={newNumber} onChange={handleNumberChange}/></div>
        <div><button type="submit">add</button></div>
        </form>
      <h2>Numbers</h2>
      <ul>
      {persons.map(person => 
        <PersonMapper key={person.id} person={person} filter ={filter} setPersons={setPersons} persons ={persons} setMessage = {setMessage} setStatus={setStatus}/>)}
      </ul>
    </div>
  )
}

export default App