import personService from '../services/personServices'
import Notification from './Notification'

const buttonFunction = (person,setPersons,persons,setMessage,setStatus) => {
  const result = window.confirm(`Delete ${person.name} ?`)
  if(result){
  personService.personDelete(person)
  .then(response => {
    setStatus(true)
    setMessage(`${person.name} has been removed sucessfully`)
    setPersons(persons.filter(personer => personer.id !== person.id))
    setTimeout(()=>{
      setMessage(null)
    },5000)
  }).catch(error => {
    setStatus(false)
    setMessage(`Information of ${person.name} has already been deleted`)
    setTimeout(()=>{
      setMessage(null)
    },5000)
  }
  )
}
}

const PersonMapper = ({person,filter,setPersons,persons,setMessage,setStatus}) => {
    if(person.name.toLowerCase().includes(filter.toLowerCase())){
    return(
      <div>
      {person.name} {person.number} 
      <button onClick= {() => {buttonFunction(person,setPersons,persons,setMessage,setStatus)}}>
        delete
      </button>
      </div>
    )
    }else{
      return null
    }
  }

export default PersonMapper