
const Notification = ({message,status}) => {
    const good = {
        color: 'green',
        background: 'lightgrey',
        fontSize: 20,
        borderStyle: 'solid',
        borderRadius: 5,
        padding: 10,
        marginBottom: 10
    }

    const bad = {
            color: 'red',
            background: 'lightgrey',
            fontSize: 20,
            borderStyle: 'solid',
            borderRadius: 5,
            padding: 10,
            marginBottom: 10
    }

    let condition = status? good:bad

    if(message === null){
        return null
    }
    return (
        <div style = {condition}>
            {message}
        </div>
    )

}

export default Notification